.switch-box {
  display: flex;
}
.switch-box .toggle {
  position: relative;
  display: inline-block;
  width: 48px;
  height: 24px;
  background-color: #96989C;
  border-radius: 30px;
  text-align: center;
  cursor: pointer;
}
.switch-box .toggle:after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #fff;
  top: 4px;
  left: 4px;
  cursor: pointer;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.08);
}
.switch-box .on-off {
  font-size: 0.625rem;
  font-weight: bold;
  text-align: center;
  height: 20px;
  line-height: 24px;
}
.switch-box .checkbox:checked + .toggle::after {
  left: 27px;
  cursor: pointer;
}
.switch-box .checkbox:checked + .toggle {
  background-color: #3e50b4;
  cursor: pointer;
}
.switch-box .checkbox:checked + .toggle .on-off {
  color: #fff;
  font-weight: bold;
}
.switch-box .checkbox {
  visibility: hidden;
  width: 0;
  height: 0;
}
.switch-box .checkbox:disabled + .toggle {
  opacity: 0.5;
}

/* new Switches */
label.switch {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  cursor: pointer;
  width: fit-content;
  position: relative;
  gap: 8px;
}
label.switch .label {
  user-select: none;
  font-size: 1rem;
  font-weight: 800;
}
label.switch input[role=switch] {
  width: 38px;
  height: 20px;
  position: absolute;
  right: 0;
  border-radius: 11px;
}
label.switch input[role=switch] ~ .state {
  display: inline-flex;
  user-select: none;
}
label.switch input[role=switch] ~ .state > .container {
  position: relative;
  display: inline-flex;
  align-items: center;
  width: 38px;
  height: 22px;
  border-radius: 11px;
  background-color: #6B6E74;
}
label.switch input[role=switch] ~ .state > .container > .position {
  position: relative;
  left: 2px;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  background: #fff;
  transition: all ease 0.3s;
}
label.switch input[role=switch]:not(:checked) ~ .state span.on {
  display: none;
}
label.switch input[role=switch]:checked ~ .state > span.off {
  display: none;
}
label.switch input[role=switch]:checked ~ .state > .container {
  background-color: #3e50b4;
}
label.switch input[role=switch]:checked ~ .state > .container > .position {
  left: 18px;
  opacity: 1;
}
label.switch input[role=switch]:disabled {
  opacity: 0;
}
label.switch input[role=switch]:disabled ~ .state {
  opacity: 0.45;
}

