.msd-card-tarjetas {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    background: #ffffff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.08);
    border-radius: 8px;
    padding: 12px 16px;
    &__header {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        font-size: 0.625rem;
        position: relative;
        b {
            font-weight: 800;
            display: inline-flex;
            margin-right: 8px;
            flex-grow: 1;
            width: 100%;
        }
        span {
            font-weight: 400;
            color: #000;
            font-size: 0.75rem;
            &.t_number {
                text-align: right;
                white-space: nowrap;
                display: inline-flex;
                margin-right: 6px;
                flex-direction: row-reverse;
            }
        }
        &__contenido-icono {
            position: absolute;
            top: -15px;
            right: 0;
        }
    }
    &__visa-logo {
        display: block;
        background: url("./img/logo-visa.svg") no-repeat top center;
        width: 48px;
        height: 48px;
        background-size: contain;
    }
    &__mastercard-logo {
        display: block;
        background: url("./img/logo_mastercard.svg") no-repeat top center;
        width: 48px;
        height: 48px;
        background-size: contain;
    }
    &__container {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        &__content {
            flex-grow: 1;
            padding-top: 4px;
        }
    }
    &__footer {
        display: flex;
        align-items: center;
        justify-content: stretch;
        font-size: 0.75rem;
        font-weight: 700;
        margin: 8px 0 -12px;
        border-top: 1px solid #ccc;
        padding: 8px 2px 12px;
        .ver-detalle {
            display: flex;
            justify-content: flex-end;
            text-align: right;
            height: auto;
            width: 100%;
            border-radius: 0;
            padding: 0 0;
            margin: 0 0;
            font-size: 0.625rem;
            min-height: auto;
         &:active {
                transform: none!important;
            }
        }

    }
    &__saldos {
        display: flex;
        flex-direction: row;
        justify-content: stretch;
        align-content: stretch;
        align-items: center;
        font-size: 0.75rem;
        flex: 1;
        &--column-left,
        &--column-right {
            display: inline-flex;
            flex-direction: column;
            flex-grow: 1;
        }
        msd-icon {
            display: inline-flex;
            margin-right: 6px;
            @media(max-width: 380px) {
                margin-right: 0;
            }
        }
    }
    &__saldo-text {
        font-size: 0.75rem;
        color: #000;
    }
    &__available {
        font-weight: 600;
        font-size: 0.75rem;
        color: #000;
    }
    &__available--number {
        color: #000;
        font-weight: 700;
        font-size: 1.125rem;
        @media only screen and (max-device-width: 380px) {
            font-size: 0.75rem;
        }
    }
    &__used_number {
        color: #000;
        font-weight: 400;
        font-size: 0.625rem;
    }
    &--border-card-dorada {
        border-left: 8px solid #fac207;
    }
    &--border-card-plata {
        border-left: 8px solid #d9dbe0;
    }
    &--border-card-negro {
        border-left: 8px solid #2c3038;
    }
    &--border-card-otros {
        border-left: 8px solid #a4aab6;
    }
    &--border-card-estandar {
        border-left: 8px solid #a4aab6;
    }
    &--border-card-internacional {
        border-left: 8px solid #a4aab6;
    }
    &--border-card-chilena {
        border: 8px solid transparent;
        background: linear-gradient(#fff 0 0) padding-box, /*white BG*/
        linear-gradient(110deg , #1e9cd0 30px, #e93238 30px) border-box;
        border-right: none;
        border-top: none;
        border-bottom: none;
    }
    &--border-card-smart {
        border: 8px solid transparent;
        background: linear-gradient(#fff 0 0) padding-box, /*white BG*/
        linear-gradient(110deg, #d17b24 30px, #a46128 30px) border-box;
        border-right: none;
        border-top: none;
        border-bottom: none;
    }
    &--border-card-smartplus {
        border: 8px solid transparent;
        background: linear-gradient(#fff 0 0) padding-box, /*white BG*/
        linear-gradient(110deg, #a19c9e 30px, #626468 30px) border-box;
        border-right: none;
        border-top: none;
        border-bottom: none;
    }
}



// CREDIT CARDS
.msd-card-credito {
    -moz-box-shadow:    0px 3px 3px 0px #ccc;
    -webkit-box-shadow: 0px 3px 3px 0px #ccc;
    box-shadow:         0px 3px 3px 0px #ccc;
    border-radius: 8px;
    margin: 10px 0;
    padding: 15px;
    width: 280px;
    height: 135px;
    color: #fff;
    position: relative;
    float: left;
    margin-right: 20px;
    cursor: pointer;

    &--visa-platinum{
      background-image: url("./img/credit-cards/credito/visa_platinum.svg");
      .msd-card-credito__container,
      .msd-card-credito__name,
      .msd-card-credito__available { color: $black_002}
    }
    &--visa-smart{
        background-image: url("./img/credit-cards/credito/visa_smart.svg");
    }
    &--visa-smart-plus{
        background-image: url("./img/credit-cards/credito/visa_smart_plus.svg");
    }
    &--visa-universal{
        background-image: url("./img/credit-cards/credito/visa_universal.svg");
    }
    &--visa-gold{
        background-image: url("./img/credit-cards/credito/visa_dorada.svg");
    }
    &--master-black{
        background-image: url("./img/credit-cards/credito/mastercard_black.svg");
    }
    &--master-estandar{
        background-image: url("./img/credit-cards/credito/mastercard_estandar.svg");
    }
    &--master-gold{
        background-image: url("./img/credit-cards/credito/mastercard_gold.svg");
    }
    &--master-smart{
        background-image: url("./img/credit-cards/credito/mastercard-smart.svg");
    }
    &--master-smart-plus{
        background-image: url("./img/credit-cards/credito/mastercard-smart-plus.svg");
    }
    &--cuentarut{
        background-image: url("./img/credit-cards/debito/cuentarut.svg");
        .msd-card-credito__container,
        .msd-card-credito__name,
        .msd-card-credito__available { color: $black_002}
    }
    &--cuentapro{
        background-image: url("./img/credit-cards/debito/cuentapro.svg");
        .msd-card-credito__container,
        .msd-card-credito__name,
        .msd-card-credito__available { color: $black_002}
    }
    &--cuenta-corriente{
        background-image: url("./img/credit-cards/debito/cuenta_corriente.svg");
        .msd-card-credito__container,
        .msd-card-credito__name,
        .msd-card-credito__available { color: $black_002}
    }
    &--ahorro-dorada{
        background-image: url("./img/credit-cards/ahorro/ahorro_dorada.svg");
    }
    &--ahorro-roja{
        background-image: url("./img/credit-cards/ahorro/ahorro_roja.svg");
    }
    &--ahorro-verde{
        background-image: url("./img/credit-cards/ahorro/ahorro_verde.svg");
    }
    &__header {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        font-size: 0.875rem;
        position: relative;
        height: 10px;
    }
    &__container {
        flex-grow: 1;
        display: block;
        flex-direction: column;
        justify-content: space-around;
        &__content {
            flex-grow: 1;
        }
    }
    &__footer {
        display: flex;
        align-items: center;
        justify-content: stretch;
        font-size: 0.75rem;
        font-weight: 700;
        margin: 16px 0 -12px;
        border-top: 1px solid #ccc;
        padding: 8px 2px 12px;
        span {
            color: $font_color2;
            white-space: nowrap;
            font-weight: 300;
        }
        .ver-detalle {
            display: flex;
            justify-content: flex-end;
            text-align: right;
            height: auto;
            width: 100%;
            border-radius: 0;
            padding: 0 0;
            margin: 0 0;
            font-size: 0.875rem;
        }
    }
    &__saldos {
        display: flex;
        flex-direction: row;
        justify-content: stretch;
        align-content: stretch;
        align-items: center;
        font-size: 0.85rem;
        flex: 1;
        margin-top: 16px;
        // text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2) ;
        &--column-left {
            display: inline-flex;
            flex-direction: column;
            flex-grow: 1;
            text-align: left;
            height: 10px;
        }
        &--column-right{
            display: inline-flex;
            flex-direction: column;
            flex-grow: 1;
            text-align: right;
        }
        msd-icon {
            display: inline-flex;
            margin-right: 6px;
        }
    }
    &__saldo-text {
        font-size: 0.75rem;
        color: #fff;
    }
    &__available {
        font-weight: 600;
        font-size: 0.75rem;
        color: #fff;
    }
    &__available--number {
        color: $font_color;
        font-weight: 800;
        font-size: 1.125rem;
        @media only screen and (max-device-width: 320px) {
            font-size: 1rem;
        }
    }
}
.msd-card-credito__used_number {
    font-weight: 800;
}
.msd-card-credito__available {
    font-weight: 600;
    font-size: 0.75rem;
    color: #fff;
    margin: 0 15px;
    text-shadow: 0px 0px 2px rgba(255, 255, 255, 0.4);
}
.display-content {
    padding: 12px 0!important;
}
.msd-icon_logo-card{
    margin-top: -10px;
  }
  .msd-card--left{
    display: block;
    float: left;
    height: auto;
    margin: 0 15px 0 0;
  }
  .msd-card--right {
    display: grid;
    float: initial;
    height: auto;
    h3{
        margin: 0;
        display: flex;
    }
    ul{
        list-style: disc;
        display: table-cell;
        margin: 0;
        li {
            margin-left: 17px;
            font-size: 0.75rem;
            float: left;
        }
    }
}

.msd-blocked{
    background-color: rgba(255, 255, 255, 0.5);
    height: 135px;
    width: 280px;
    margin: -15px;
    position: absolute;
    z-index: 1;
    padding: 40px 0;
}
.msd-blocked__temp--left {
    background-color: red;
    color: #fff;
    margin: auto;
    border-radius: 10px;
    border: 2px solid #fff;
    padding: 8px;
    font-size: 0.75rem;
    width: 80%;
    line-height: 14px;
    msd-icon{
        float: left;
        display: block;
    }
    .label--bold{
        font-weight: bold;
        font-size: 0.875rem;
    }
     label{
        display: block;
        padding-left: 30px;
    }
}

.msd-blocked.hidden{
    display: none;
}